.chat-app {
  position: fixed;
  bottom: 24px;
  right: 0px;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-end;
  min-width: 350px;
  z-index: 1030;
  padding: 0 1rem;
}
.chat-app ::-webkit-scrollbar {
  width: 2px;
}
.chat-app ::-webkit-scrollbar-track {
  background: var(--card-bg);
}
.chat-app ::-webkit-scrollbar-thumb {
  background: var(--scrollbar-thumb-color);
  border-radius: 10px;
  max-height: 30px;
}
.chat-app ::-webkit-scrollbar-thumb:hover {
  background: var(--gray-500);
}
.chat-app .chat_left_section {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-end;
}
.chat-app .chat_left_section .chat-window {
  height: 582px;
  width: 350px;
  position: relative;
  box-shadow: 0px 2px 6px rgba(17, 43, 66, 0.08), 0px 1px 4px rgba(17, 43, 66, 0.1);
  background: var(--card-bg);
  border-radius: 6px;
  margin-bottom: 1rem;
  margin-right: 5px;
  border: 1px solid var(--dark-border-color);
}
.chat-app .chat_right_section {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-end;
}
.chat-app .chat_right_section .chat-element {
  height: 582px;
  width: 350px;
  position: relative;
  box-shadow: 0px 2px 6px rgba(17, 43, 66, 0.08), 0px 1px 4px rgba(17, 43, 66, 0.1);
  background: var(--card-bg);
  border-radius: 6px;
  margin-bottom: 1rem;
  border: 1px solid var(--dark-border-color);
}
.chat-app .chat_right_section .chat-element .chat-container {
  padding: 0;
  margin: 0;
}
.chat-app .chat_right_section .chat-element .chat-cross-button {
  display: none;
  color: var(--gray-700);
  cursor: pointer;
  position: absolute;
  top: 12px;
  right: 8px;
}
.chat-app .chat_right_section .chat-bubble {
  cursor: pointer;
  background: #181f2f;
  color: var(--white);
  border-radius: 100px;
  font-weight: bold;
  display: flex;
  align-items: center;
  width: initial;
}
.chat-app .chat_right_section .chat-bubble svg {
  fill: var(--white);
}
.chat-app .chat_right_section .chat-bubble:hover {
  box-shadow: var(--shadow-lg);
}
.chat-app .chat_right_section .chat-bubble .chat-message-icon {
  margin-right: var(--margin-sm);
  display: flex;
  align-items: center;
  justify-content: center;
}
.chat-app .chat_right_section .chat-bubble-closed {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 22px;
}
.chat-app .chat_right_section .chat-bubble-closed .cross-icon {
  width: 100%;
}

.ask-to-join-container strong {
  white-space: break-spaces;
}

.infobutton {
  height: 50px;
  display: flex;
  color: var(--text-color);
  flex-grow: 1;
  flex-direction: column;
  font-size: 0.875rem;
  align-items: center;
  text-transform: capitalize;
  justify-content: center;
  outline: none;
  padding: 0;
  cursor: pointer;
  background: none;
  border: 0;
  padding-top: 4px;
  margin: 0;
}

.infobutton.active {
  border-bottom: 4px solid #adadad;
}

.gname[readonly] {
  border: none;
  outline: none;
}

.gname {
  text-align: center;
}

.tag-blot-hidden {
  display: none;
}

.contributors_count,
.contributors_close,
.contributors_count_icon {
  cursor: pointer;
}

.chat-media {
  background-color: #f0f2f5;
}
.chat-media .mediatabcontainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.chat-media .mediatabcontainer video,
.chat-media .mediatabcontainer a {
  display: flex;
  width: 136px !important;
  height: 100px;
  margin: 8px;
}
.chat-media .mediatabcontainer video img,
.chat-media .mediatabcontainer a img {
  -o-object-fit: cover !important;
     object-fit: cover !important;
  width: 100%;
  height: 100%;
}
.chat-media .linkstabcontainer .linksrow {
  margin: 8px;
  display: block;
  padding: 8px;
  background: #f4f5f6;
  width: 100%;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  border-top-right-radius: 8px;
  margin: 8px;
